import React from "react";
import { useForm, useStep } from "react-hooks-helper";
import { DetailsPage } from "./DetailsPage";
import { Page1 } from "./Page1";
import { Page2 } from "./Page2";
import { Page3 } from "./Page3";
import { Page4 } from "./Page4";
import { Page5 } from "./Page5";
import { Page6 } from "./Page6";
import { Page7 } from "./Page7";
import { SubmitPage } from "./SubmitPage";
const defaultData = {
  firstName: "",
  lastName: "",
  dob: "",
  gender: "",
  nationality: "",
  countryResidence: "",
  phoneNo: "",
  email: "",
  visitDubai: [],
  familydubai: [],
  stayDubai: [],
  resonforDubai: [],
  industryData: [],
  interestData: [],
  savingData: [],
  educationData: [],
  comeDubai: [],
};
const steps = [
  { id: "detailsPage" },
  { id: "page1" },
  { id: "page2" },
  { id: "page3" },
  { id: "page4" },
  { id: "page5" },
  { id: "page6" },
  { id: "page7" },
  { id: "submitPage" },
];
export const Form = () => {
  const [formData, setForm] = useForm(defaultData);

  const { step, navigation } = useStep({ steps, initialStep: 0 });

  const props = { formData, setForm, navigation };
  switch (step.id) {
    case "detailsPage":
      return <DetailsPage {...props} />;
    case "page1":
      return <Page1 {...props} />;
    case "page2":
      return <Page2 {...props} />;
    case "page3":
      return <Page3 {...props} />;
    case "page4":
      return <Page4 {...props} />;
    case "page5":
      return <Page5 {...props} />;
    case "page6":
      return <Page6 {...props} />;
    case "page7":
      return <Page7 {...props} />;
    case "submitPage":
      return <SubmitPage {...props} />;
  }

  return <div></div>;
};
