import React from "react";
import { Container, Button, Grid } from "@material-ui/core";
import SaveIcon from "@material-ui/icons/Save";
import {
  createMuiTheme,
  withStyles,
  makeStyles,
  ThemeProvider,
} from "@material-ui/core/styles";
import CardMedia from "@material-ui/core/CardMedia";
import { OptionButton } from "./OptionButton";
import "../CSS/Main.css";
import Group137 from "../SvgComponent/Group137.svg";
import F44D from "../SvgComponent/1F44D.svg";
import F5D3 from "../SvgComponent/1F5D3.svg";
import update from "immutability-helper";

export const Page1 = ({ formData, setForm, navigation }) => {
  const { visitDubai, familydubai, stayDubai } = formData;

  const [yesNoVisitBtn, setVisit] = React.useState([
    {
      id: 0,
      title: "No",
      isSelected: false,
      icon: F44D,
      category: "visitDubai",
    },
    {
      id: 1,
      title: "Yes",
      isSelected: false,
      icon: Group137,
      category: "visitDubai",
    },
  ]);

  const [familyDubai, setFamilyDubai] = React.useState([
    {
      id: 0,
      title: "No",
      isSelected: false,
      icon: F44D,
      category: "familyDubai",
    },
    {
      id: 1,
      title: "Yes",
      isSelected: false,
      icon: Group137,
      category: "familyDubai",
    },
  ]);

  const [stayDubaibtn, setStayDubaiBtn] = React.useState([
    {
      id: 0,
      title: "Upto 3 months",
      isSelected: false,
      icon: F5D3,
      category: "stayDubaibtn",
    },
    {
      id: 1,
      title: "3 to 6 months",
      isSelected: false,
      icon: F5D3,
      category: "stayDubaibtn",
    },
    {
      id: 2,
      title: "6 to 12 months",
      isSelected: false,
      icon: F5D3,
      category: "stayDubaibtn",
    },
    {
      id: 3,
      title: "+12 months",
      isSelected: false,
      icon: F5D3,
      category: "stayDubaibtn",
    },
    {
      id: 4,
      title: "+3 years",
      isSelected: false,
      icon: F5D3,
      category: "stayDubaibtn",
    },
  ]);

  const setSize = (arraydata, id, category, btnSelected) => {
    var data = arraydata;
    var commentIndex = data.findIndex(function (c) {
      return c.id == id;
    });

    var updatedComment = update(data[commentIndex], {
      isSelected: { $set: btnSelected },
    });

    var newData = update(data, {
      $splice: [[commentIndex, 1, updatedComment]],
    });
    switch (category) {
      case "visitDubai":
        visitDubai.length = 0;
        setVisit(newData);
        visitDubai.push(newData.filter((e) => e.isSelected === true));
        break;
      case "stayDubaibtn":
        familydubai.length = 0;
        setStayDubaiBtn(newData);
        familydubai.push(newData.filter((e) => e.isSelected === true));
        break;
      case "familyDubai":
        stayDubai.length = 0;
        setFamilyDubai(newData);
        stayDubai.push(newData.filter((e) => e.isSelected === true));
        break;
    }
    console.log(formData);
  };

  return (
    <Container>
      <Grid container direction="column" spacing={3}>
        <Grid item xs={3} sm={3} md={3} lg={3} xl={3} className="headline1">
          {"digg "}
        </Grid>

        <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          {"Have you ever visited Duabi?"}
        </Grid>
        <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          {yesNoVisitBtn.map((button, key) => (
            <OptionButton
              key={key}
              id={button.id}
              title={button.title}
              icon={button.icon}
              category={button.category}
              isSelected={button.isSelected}
              setSelectbtn={setSize}
              visitData={yesNoVisitBtn}
            />
          ))}
        </Grid>
        <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          {"Do you have any friends or family in Dubai?"}
        </Grid>
        <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          {familyDubai.map((button, key) => (
            <OptionButton
              key={key}
              id={button.id}
              title={button.title}
              icon={button.icon}
              category={button.category}
              isSelected={button.isSelected}
              setSelectbtn={setSize}
              visitData={familyDubai}
            />
          ))}
        </Grid>
        <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          {"How long do you want to stay in Dubai ?"}
        </Grid>
        <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          {stayDubaibtn.map((button, key) => (
            <OptionButton
              key={key}
              id={button.id}
              title={button.title}
              icon={button.icon}
              category={button.category}
              isSelected={button.isSelected}
              setSelectbtn={setSize}
              visitData={stayDubaibtn}
            />
          ))}
        </Grid>
        <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          <Button
            variant="contained"
            style={custombtn}
            onClick={() => navigation.previous()}
          >
            {" Back"}
          </Button>
          <Button
            variant="contained"
            style={custombtn}
            onClick={() => navigation.next()}
          >
            {" Next"}
          </Button>
        </Grid>
      </Grid>
    </Container>
  );
};
const custombtn = {
  background: "linear-gradient(90deg, #4734E1 30%, #8E27EA 90%)",
  borderRadius: "100px",
  border: 0,
  color: "white",
  height: 48,
  padding: "8px 54px",
  boxShadow: "0 3px 5px 2px rgba(255, 105, 135, .3)",
  marginRight: "32px",
};
