import React from "react";
import {
  Container,
  TextField,
  FormGroup,
  FormControlLabel,
  Checkbox,
  FormLabel,
  Radio,
  RadioGroup,
  FormControl,
  InputLabel,
  Select,
  Button,
  Grid,
} from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";
import { CustomTextField } from "./CustomTextField";
import "../CSS/Main.css";
import Path969 from "../SvgComponent/Path969.svg";
import Path970 from "../SvgComponent/Path970.svg";
import man from "../SvgComponent/man.svg";
import Path968 from "../SvgComponent/Path968.svg";
export const DetailsPage = ({ formData, setForm, navigation }) => {
  const {
    firstName,
    lastName,
    dob,  
    gender,
    nationality,
    countryResidence,
    phoneNo,
    email,
  } = formData;
  console.log(formData);
  const classes = useStyles();
  return (
    <Container>
      <Grid container direction="row" alignItems="center">
        <Grid item md={6} lg={7} xl={6} className="hide">
          <img src={Path969} className="backimg" />
          <Grid item lg={3} className="headline">
            {"digg"}
          </Grid>
          <Grid item lg={3} className="top-right">
            <img src={Path970} className="backimg" />
          </Grid>
          <Grid item lg={5} className="centered">
            <img src={Path968} className="" />
          </Grid>
          <Grid item lg={5} className="centered">
            <img src={man} className="" />
          </Grid>

          <Grid item lg={5} className="bottom-left-div1">
            {
              "Coming to Dubai with One Step Dubai has been the best decision of my life. I am now living in Dubai with a good job and I will be getting married next year!"
            }
          </Grid>
          <Grid item lg={5} className="bottom-left-div2">
            {"Mohammed, One Step Dubai student Pakistan"}
          </Grid>
        </Grid>
        <Grid
          container
          direction="row"
          justify="center"
          alignItems="center"
          spacing={1}
          xs={12}
          sm={12}
          md={6}
          lg={5}
          xl={6}
        >
          <Grid item xs={12}>
            <h1 className={classes.paper}>{"Apply now to work in Dubai "}</h1>
          </Grid>
          <Grid item xs={12} sm={11} md={5} lg={6} xl={5}>
            <CustomTextField
              name="firstName"
              fullWidth="true"
              label="First Name"
              variant="filled"
              value={firstName}
              onChange={setForm}
              size="small"
              required
              className={classes.margin}
            />
          </Grid>
          <Grid item xs={12} sm={12} md={5} lg={6} xl={5}>
            <CustomTextField
              name="lastName"
              label="Last Name"
              fullWidth="true"
              variant="filled"
              value={lastName}
              size="small"
              className={classes.margin}
              onChange={setForm}
            />
          </Grid>
          <Grid item xs={12} sm={12} md={5} lg={6} xl={5}>
            <TextField
              id="date"
              name="dob"
              label="Birthday"
              type="date"
              variant="filled"
              value={dob}
              size="small"
              fullWidth="true"
              className={classes.margin}
              onChange={setForm}
              InputLabelProps={{
                shrink: true,
              }}
            />
          </Grid>
          <Grid item xs={12} sm={12} md={5} lg={6} xl={5}>
            <FormControl className={classes.margin} fullWidth="true">
              <FormLabel>Gender</FormLabel>
              <RadioGroup
                aria-label="gender"
                name="gender"
                value={gender}
                row
                size="small"
                onChange={setForm}
              >
                <FormControlLabel
                  value="male"
                  control={<Radio />}
                  label="Male"
                />
                <FormControlLabel
                  value="female"
                  control={<Radio />}
                  label="Female"
                />
                <FormControlLabel
                  value="other"
                  control={<Radio />}
                  label="Other"
                />
              </RadioGroup>
            </FormControl>
          </Grid>
          <Grid item xs={12} sm={12} md={5} lg={6} xl={5}>
            <FormControl
              variant="filled"
              size="small"
              className={classes.margin}
              fullWidth="true"
            >
              <InputLabel htmlFor="filled-nationality">Nationality</InputLabel>
              <Select
                native
                value={nationality}
                inputProps={{
                  name: "nationality",
                  id: "filled-nationality",
                }}
                onChange={setForm}
              >
                {countyoptions.map((option) => (
                  <option value={option.value} kay={option.value}>
                    {option.label}
                  </option>
                ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={12} sm={12} md={5} lg={6} xl={5}>
            <FormControl
              variant="filled"
              fullWidth="true"
              size="small"
              className={classes.margin}
            >
              <InputLabel htmlFor="filled-residence">
                Country of residence
              </InputLabel>
              <Select
                native
                value={countryResidence}
                onChange={setForm}
                inputProps={{
                  name: "countryResidence",
                  id: "filled-residence",
                }}
              >
                {countyoptions.map((option) => (
                  <option value={option.value} kay={option.value}>
                    {option.label}
                  </option>
                ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
            {"How we can contact you ? "}
          </Grid>
          <Grid item xs={12} sm={12} md={5} lg={6} xl={5}>
            <CustomTextField
              size="small"
              className={classes.margin}
              name="phoneNo"
              label="Phone No"
              fullWidth="true"
              variant="filled"
              value={phoneNo}
              onChange={setForm}
            />
          </Grid>

          <Grid item xs={12} sm={12} md={5} lg={6} xl={5}>
            <CustomTextField
              name="email"
              label="Email"
              fullWidth="true"
              variant="filled"
              size="small"
              value={email}
              onChange={setForm}
              className={classes.margin}
            />
          </Grid>

          <Grid
            container
            item
            xs={12}
            sm={12}
            md={12}
            lg={12}
            xl={12}
            justify="flex-end"
          >
            <Button
              variant="contained"
              style={custombtn}
              onClick={() => navigation.next()}
            >
              {" Apply Now"}
            </Button>
          </Grid>
        </Grid>
      </Grid>
    </Container>
  );
};
const countyoptions = [
  {
    label: "",
    value: "",
  },
  {
    label: "Indian",
    value: "indian",
  },
  {
    label: "United Arab Emirates",
    value: "uae",
  },
];

//style for material ui component
const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
  },
  margin: {
    margin: theme.spacing(1),
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: "center",
  },
}));
const custombtn = {
  background: "linear-gradient(45deg, #4734E1 30%, #8E27EA 90%)",
  borderRadius: "100px",
  border: 0,
  color: "white",
  height: 48,
  padding: "0 30px",
  boxShadow: "0 3px 5px 2px rgba(255, 105, 135, .3)",
};
