import React from "react";
import { Container, Button, Grid } from "@material-ui/core";
import { OptionButton } from "./OptionButton";
import "../CSS/Main.css";
import Group137 from "../SvgComponent/Group137.svg";
import F44D from "../SvgComponent/1F44D.svg";
import F5D3 from "../SvgComponent/1F5D3.svg";
import update from "immutability-helper";
export const Page7 = ({ formData, setForm, navigation }) => {
  const { comeDubai } = formData;
  const [comeToDubai, setcomeToDubai] = React.useState([
    {
      id: 0,
      title: "Right now",
      isSelected: false,
      icon: F5D3,
      category: "comeToDubai",
    },
    {
      id: 1,
      title: "0 to 3 months",
      isSelected: false,
      icon: F5D3,
      category: "comeToDubai",
    },
    {
      id: 2,
      title: "3 to 6 months",
      isSelected: false,
      icon: F5D3,
      category: "comeToDubai",
    },
    {
      id: 3,
      title: "6 to 12 months",
      isSelected: false,
      icon: F5D3,
      category: "comeToDubai",
    },
    {
      id: 4,
      title: "After 1 year",
      isSelected: false,
      icon: F5D3,
      category: "comeToDubai",
    },
    {
      id: 5,
      title: "I'm not sure",
      isSelected: false,
      icon: F5D3,
      category: "comeToDubai",
    },
  ]);
  const setcomeToVisit = (arraydata, id, category, btnSelected) => {
    var data = arraydata;
    var commentIndex = data.findIndex(function (c) {
      return c.id == id;
    });

    var updatedComment = update(data[commentIndex], {
      isSelected: { $set: btnSelected },
    });

    var newData = update(data, {
      $splice: [[commentIndex, 1, updatedComment]],
    });

    comeDubai.length = 0;
    setcomeToDubai(newData);
    comeDubai.push(newData.filter((e) => e.isSelected === true));
    console.log(formData);
  };
  return (
    <Container>
      <Grid container direction="column" spacing={3}>
        <Grid item xs={3} sm={3} md={3} lg={3} xl={3} className="headline1">
          {"digg "}
        </Grid>

        <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          {"How soon do you want to come to Dubai?"}
        </Grid>
        <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          {comeToDubai.map((button, key) => (
            <OptionButton
              key={key}
              id={button.id}
              title={button.title}
              icon={button.icon}
              category={button.category}
              isSelected={button.isSelected}
              setSelectbtn={setcomeToVisit}
              visitData={comeToDubai}
            />
          ))}
        </Grid>

        <Grid item xs={12} sm={12} md={12} lg={12} xl={12}>
          <Button
            variant="contained"
            style={custombtn}
            onClick={() => navigation.previous()}
          >
            {" Back"}
          </Button>
          <Button
            variant="contained"
            style={custombtn}
            onClick={() => navigation.next()}
          >
            {" Next"}
          </Button>
        </Grid>
      </Grid>
    </Container>
  );
};

const custombtn = {
  background: "linear-gradient(90deg, #4734E1 30%, #8E27EA 90%)",
  borderRadius: "100px",
  border: 0,
  color: "white",
  height: 48,
  padding: "8px 54px",
  boxShadow: "0 3px 5px 2px rgba(255, 105, 135, .3)",
  marginRight: "32px",
};
